@include bundle($type: 'shared') {

  .andes-snackbar {
    position: fixed;
    z-index: $snackbar-z-index;
    width: 100%;
    bottom: 0;
    display: table;
    border-collapse: collapse;
    border-spacing: em(10);
    color: $andes-white;
    left: 50%;
    transform: translateX(-50%);
    animation-duration: $snackbar-animation-duration;

    &--default {
      background-color: $snackbar-default;
    }

    &__message,
    &__action {
      display: table-cell;
      vertical-align: middle;
    }

    &__action {
      color: $andes-white !important;
      background: inherit;
      border: 0;
      font-size: inherit;
      text-transform: uppercase;
      padding-left: 0;
      height: 100%;
      text-align: right;
      white-space: nowrap;
      text-decoration: none !important;

      &:hover {
        text-decoration: underline;
      }
    }

    &--animate-show {
      animation-name: snackbar-show;
    }

    &--animate-hide {
      animation-name: snackbar-hide;
      animation-fill-mode: forwards;
    }
  }

}
