$snackbar-z-index: $z-index + 1;
$snackbar-max-width: em(600);
$snackbar-gutter: $andes-spacing-32;
$snackbar-gutter-s: $andes-spacing-24;
$snackbar-gutter-xs: $andes-spacing-16;
$snackbar-gutter-xss: em(6);
$snackbar-border-radius: $andes-spacing-4;
$snackbar-line-height-desktop: $andes-spacing-20;
$snackbar-line-height-mobile: em(18);

$snackbar-error: $andes-error-red;
$snackbar-success: $andes-success-green;
$snackbar-default: $andes-gray-800;

$snackbar-animation-duration: 0.5s;

@keyframes snackbar-show {
  from {
    opacity: 0;
    transform: translate3d(-50%, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(-50%, 0, 0);
  }
}

@keyframes snackbar-hide {
  0% {
    transform: translate3d(-50%, 0, 0);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translate3d(-50%, 100%, 0);
  }
}
