@include bundle($type: 'desktop') {

  .andes-snackbar {
    max-width: $snackbar-max-width;
    bottom: $snackbar-gutter;
    font-size: $font-size-16;
    border-radius: $snackbar-border-radius;
    margin-left: auto;
    margin-right: auto;
    line-height: $snackbar-line-height-desktop;

    &__action,
    &__message {
      padding: $snackbar-gutter-s $snackbar-gutter;
      border-radius: $snackbar-border-radius;
    }
  }

}
